import { Injectable, TemplateRef } from '@angular/core';
import { NzConfigService } from 'ng-zorro-antd/core/config';
import { NzNotificationService } from 'ng-zorro-antd/notification';

@Injectable()
export class ToastService {
  private template: TemplateRef<any>;

  constructor(
    private nzConfigService: NzConfigService,
    private notificationService: NzNotificationService
  ) {
    this.nzConfigService.set('notification', {
      nzDuration: 5000,
      nzPlacement: 'bottomRight'
    });
  }

  connect(template: TemplateRef<any>): void {
    this.template = template;
  }

  success(message: string, translate: boolean = false): void {
    this.createToast(message, translate, 'icon-checkmark-circle', 'success');
  }

  error(message: string, translate: boolean = false): void {
    this.createToast(message, translate, 'icon-cross-circle', 'error');
  }

  warning(message: string, translate: boolean = false): void {
    this.createToast(message, translate, 'icon-warning', 'warning');
  }

  private createToast(
    message: string,
    translate: boolean,
    icon: string,
    type: 'success' | 'error' | 'warning'
  ): void {
    this.notificationService.template(this.template, {
      nzData: { message, translate, icon },
      nzClass: `sof-toast-${type}`
    });
  }
}
