import {
  AfterViewInit,
  Component,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ToastService } from './services/toast.service';

@Component({
  selector: 'sof-toast-hook',
  styleUrls: ['./toast-hook.component.scss'],
  encapsulation: ViewEncapsulation.None, // so all css can stay with the component
  template: `
    <ng-template let-toast="data">
      <div class="content">
        <sof-svg-icon [icon]="toast.icon" size="20"></sof-svg-icon>
        <div class="message">
          {{ toast.translate ? (toast.message | translate) : toast.message }}
        </div>
      </div>
    </ng-template>
  `
})
export class ToastHookComponent implements AfterViewInit {
  @ViewChild(TemplateRef) template: TemplateRef<any>;

  constructor(private toastService: ToastService) {}

  ngAfterViewInit(): void {
    this.toastService.connect(this.template);
  }
}
